*{
  padding: 0;
  margin: 0;
  text-decoration: none !important;
}

p{
  font-size: 1vw;
}

h1{
  font-size: 2vw;
}

option{
  font-size: 1vw;
}

.video-selector{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 1vw;
}

.video-selector-button{
  margin: 0 1vw;
  width: 2vw;
  height: 2vw;
  font-size: 1.5vw;
  border-color: transparent;
  border-radius: 1vw;
}

.video-selector-button:hover{
  background-color: rgb(166, 248, 248);
  transition: .5s;
}

.video-selector-button-active{
  background-color: rgb(65, 252, 252);
}

.video-unactive{
  display:none;
}

.videos-answers{
  display: flex;
  font-size: 2vw;
  gap:2vw;
}

.videos-selector-answers
{
  text-align: center;
}

.form-control{
  padding: 0.3vw;
  border-radius: 1vw;
  border-color: transparent;
  font-size: 1vw;
}
.videos-text{
  height: 200px;
  width: 800px;
  overflow: auto;
}

.task{
  text-align: center;
  display: flex;
  flex-direction: column;
  gap:1vw;
  border: double;
  border-width: 0.2vw;
  border-radius: 1vw;
  border-color: rgba(176, 215, 248, 0.411);
}

.task-text{
  text-align: left;
  align-self: center;
}

video {
  width: 40vw;
  height: 20vw;
}

.unit-task-unactive{
  display: none;
}

.unit-buttons{
  position:fixed;
  z-index: 9999;
  display:flex;
  flex-direction: column;
  justify-content: space-evenly;
  top:15vw;
  gap: 1.5vw;
  width: 20vw;
  left: 5vw;
}
.unit{
  width: 100vw;
}

.unit-button{
  border-radius: 2vw;
  background: rgb(136, 186, 209);
  border-width: 0.1vw;
  width: 15vw;
  font-size: 2vw;
  text-align: center;
  color: black;
  transition: .3s;
}

.unit-button:hover{
  background: rgb(118, 172, 252);
  font-size: 2.2vw !important;
  border-width: 0;
  color: rgb(90, 90, 90);
  transition: 0.1s;
}

.unit-tasks{
  color: aliceblue !important;
  display: flex;
  flex-direction: column;
  align-content: center;
  width: 50vw;
  left: 23vw;
  gap:1vw;
  position: relative;
  backdrop-filter: blur(10px);
  padding: 2vw;
}
.navbar {
  background-color: rgb(116, 189, 218);
  height: 5vw;
}
.unit-background{
  position: fixed;
  top: -2vw;
  z-index: -1;
}
.unit-background img{
  width: 100vw;
  filter:brightness(30%)
}